    showPiece1 = () => {
        this.setState({
            codeLoad: true,
        })
        var i = 0;
        var txt = this.state.code.piece1;
        var txtHolder = "";
        var speed = 15;
        
        setTimeout(() => {
            this.setState({
                codeLoad: false,
                infoLoad: true,
                infoCurrent: {
                    number: this.state.info.piece1.number,
                    title: this.state.info.piece1.title,
                    subtitle: this.state.info.piece1.subtitle,
                    thumbnail: this.state.info.piece1.thumbnail,
                    paragraph1: this.state.info.piece1.paragraph1,
                    paragraph2: this.state.info.piece1.paragraph2,
                    paragraph3: this.state.info.piece1.paragraph3,
                    image1: this.state.info.piece1.image1,
                    image2: this.state.info.piece1.image2,
                    image3: this.state.info.piece1.image3,
                    linkWeb: this.state.info.piece1.linkWeb,
                    linkGit: this.state.info.piece1.linkGit
                },
            });
        }, 1500)
        
        const typeWriter = () => {
            if (i < txt.length) {
            txtHolder += txt.charAt(i)
            this.setState({
                currentCode: txtHolder,
            })
            i++;
            setTimeout(typeWriter, speed);
            }
        }
        return typeWriter()
        
        
    }
    
